<template>
  <v-container>
    <v-overlay :value="transactionInProgress">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
    <v-layout row wrap>
    <custody-selection name="in-page" :value="activateWalletSelection"></custody-selection>
      <v-row>
        <v-col cols="12" v-for="wallet of visitedWalletsDetails" :key="wallet.address">
          <v-card outlined elevation="3" class="pa-3" @click="selectThisWallet(wallet.address)">
            <v-row>
              <v-col cols="1" align-self="center">
                <eth-avatar-icon :value="wallet.address"></eth-avatar-icon>
              </v-col>
              <v-col cols="6" align-self="center">
                {{wallet.address}}
              </v-col>
              <v-col cols="5" align-self="center">
                {{ toNumber(toGWei(wallet.balance))}} GWei
              </v-col>
            </v-row>
          </v-card>
        </v-col>
        <v-col cols="12" >
          <v-card outlined elevation="3" class="pa-3">
            <v-btn color="secondary" @click="activateWalletSelection=true">Visit the custody service to get a new wallet</v-btn>
          </v-card>
        </v-col>
        <v-col cols="12" v-if="currentAddress">
          <v-card outlined elevation="3" class="pa-3">
            <v-card-subtitle>Crypto transfer - for convenience only</v-card-subtitle>
            <v-row>
              <v-col cols="5" align-self="center">
                <v-text-field label="amount of GWei" type="number" v-model="cryptoGWeiAmount"></v-text-field></v-col>
              <v-col cols="6" align-self="center">
                <v-text-field label="target account address" type="text" v-model="cryptoTransferTarget" ></v-text-field></v-col>
              <v-col cols="1" align-self="center">
                <v-btn @click="executeCryptoTransfer" >Ok</v-btn></v-col>
            </v-row>
            
           
            
          </v-card>
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import { storage, ethereum, storageNames, ensureFlag, connectToNode } from "../lib/api";
import { toWei, toNumber, toGWei } from "../lib/numbers";
import { notifySuccess, walletAuthDialog } from '../store';
import EthAvatarIcon from "./EthAvatarIcon.vue";
import { Crypto } from "@saturn-chain/dlt-tx-data-functions";


const visitedWalletName = storageNames.walletAddresses;
export default {
  components: {
    EthAvatarIcon, 
    CustodySelection: () => import("./WalletCustodySelection.vue") // Very odd behaviour but this is to avoid the did you register the component correctly? For recursive components, make sure to provide the "name" option error
  },
  data() {
    return {
      transactionInProgress: false,
      visitedWallets: [],
      visitedWalletsDetails: [],
      activateWalletSelection: false,
      cryptoGWeiAmount: undefined,
      cryptoTransferTarget: undefined,
    }
  },
  async mounted() {
    this.visitedWallets = await storage().getItem(visitedWalletName) || [];
    await this.fillWalletDetails();
  },
  computed: {
    currentAddress() {
      return this.$store.getters.currentWalletAddress;
    },
  },
  methods: {
    toGWei, toNumber,
    ...mapActions([ "applyCurrentWalletAddress"]),
    async fillWalletDetails() {
      const eth = ethereum();
      const arrayResult = [];
      for (const address of this.visitedWallets) {
        const detail = {address};
        detail.balance = await eth.getBalance(address)
        arrayResult.push(detail);
      }
      this.visitedWalletsDetails = arrayResult;
    },

    async selectThisWallet(address) {
      await addVisitedWallet(address);
      await this.applyCurrentWalletAddress(address);
    },

    async executeCryptoTransfer() {
      if (!this.cryptoGWeiAmount || this.cryptoGWeiAmount<=0) return;
      if (!this.cryptoTransferTarget) return;
      await ensureFlag(this, 'transactionInProgress', async ()=>{
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const amount = toWei(this.cryptoGWeiAmount, "gwei");
        await Crypto.transfer(intf.send({maxGas:30000, amount: "0x"+amount.toString(16) }), this.cryptoTransferTarget);
        notifySuccess(`Transfer of ${this.cryptoGWeiAmount} GWei to ${this.cryptoTransferTarget} completed.`)
        await this.fillWalletDetails();
        this.cryptoGWeiAmount = undefined;
        this.cryptoTransferTarget = undefined;
      })
    }
  }
}
export async function addVisitedWallet(address) {
  let current = await storage().getItem(visitedWalletName) || [];
  current = current.filter(a=>a!==address); // remove from the array if it exists
  current.unshift(address) ; // add in front of the array
  await storage().setItem(visitedWalletName, current);
}
export async function getFirstWallet() {
  const current = await storage().getItem(visitedWalletName) || [];
  if(current && Array.isArray(current) && current.length>0) {
    return current[0];
  } else return undefined;
}
</script>

