import { create } from "ethereum-blockies";

export function createIcon(address) {
  if (!address) address = "0x" // the icon of the zero address;
  const canvas = create({
    // All options are optional
    seed: address.toLowerCase(), // seed used to generate icon data, default: random
    // color: "#dfe", // to manually specify the icon color, default: random
    bgcolor: "#fff", // choose a different background color, default: random
    size: 16, // width/height of the icon in blocks, default: 8
    scale: 4, // width/height of each block in pixels, default: 4
    spotcolor: "#000", // each pixel has a 13% chance of being of a third color,
    // default: random. Set to -1 to disable it. These "spots" create structures
    // that look like eyes, mouths and noses.
  });
  return canvas;
}
