<template>
  <img ref="container">
</template>

<script>
import { createIcon } from "../lib/blockies";
export default {
  props: {
    value: undefined,
  },
  mounted() {
    this.setCanvas(this.value);
  },
  computed: {
    address() {
      return this.value;
    },
  },
  watch: {
    address(v) {
      this.setCanvas(v);
    },
  },
  methods: {
    setCanvas(address) {
      let container = this.$refs.container;
      // console.log("EthAvatar", container, address);
      if (!container) return;
      // container = container.parentNode;
      // // removing existing content
      // while (container.hasChildNodes())
      //   container.removeChild(container.firstElementChild);
      // container.appendChild(createIcon(address));
      container.src = createIcon(address).toDataURL();
    },
  },
};
</script>

<style></style>
