<template>
  <v-container>
    <v-layout row wrap>
      <v-row>
        <v-col cols="12">
          <h1>Prepare the configuration</h1>
        </v-col>
        <v-col cols="12" v-for="v in asArrayOf" :key="v.name">
          <v-card outlined elevation="3" class="pa-4">
            <v-row class="align-center align-self-baseline ">
              <v-col cols="6" outlined tile>
                <v-text-field
                  :label="v.name"
                  append-icon="mdi-close"
                  v-model="variables[v.name]"
                  :rules="[rules.notNull, v.valid]"
                  @click:append="variables[v.name]=''"
                ></v-text-field>
              </v-col>
              <v-col cols="6" outlined tile>
                <span>{{ v.desc }}</span>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="1" >
          <v-icon color="green" x-large v-if="isConfigValid">mdi-thumb-up</v-icon>
          <v-icon color="red" x-large v-else>mdi-thumb-down</v-icon>
        </v-col>
        <v-col cols="11">
          <v-btn color="primary"
            v-if="isConfigValid"
            @click="saveConfiguration"
          >Save configuration</v-btn>
        </v-col>
        <v-col cols="2" align-self="center" @click="saveConfigToFile">
          <v-btn >Download config</v-btn>
        </v-col>
        <v-col cols="4" align-self="center">
          <v-file-input show-size label="place the file that contains the saved config" v-model="configFile"></v-file-input>
        </v-col>
        <v-col cols="6" align-self="center" @click="loadConfigFromFile">
          <v-btn >Load config</v-btn>
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import { storage, storageNames } from "../lib/api";
import { download } from "../lib/files";

function isValidUrl(url) {
  if(!url) return false;
  try {
    const u = new URL(url)
    return !!u
  } catch (error) {
    return false;
  }
}

function isValidEthereumAddress(address) {
  if (!address) return false;
  if (typeof address !=="string") return false;
  return (address.length === 42 && address.startsWith("0x"));
}

const storageName = storageNames.mainConfig;
const variableDefinitions = {
  IOUAddress: {
    typeof: "string",
    order: 1,
    desc: "The address of the IOU smart contract in the blockchain",
    valid: v=>isValidEthereumAddress(v) || "Enter an Ethereum address",
  },
  walletCustodyService: {
    typeof: "string",
    order: 2,
    desc: "The url of the wallet custody service",
    valid: v=>isValidUrl(v) || 'Enter a valid url',
  },
  walletCustodyUI: {
    typeof: "string",
    order: 3,
    desc: "The url of the wallet custody interface",
    valid: v=>isValidUrl(v) || 'Enter a valid url',
  },
  web3WebSocketUrl: {
    typeof: "string",
    order: 4,
    desc: "The ws url of the web3 compatible node (typically infura url)",
    valid: v=>(v && isValidUrl(v) /*&& v.startsWith("ws")*/)  || 'Enter a valid websocket url',
  },
  blockchainExplorer: {
    typeof: "string",
    order: 5,
    desc: "The blockchain explorer web site (typically etherscan). Should implements /tx/<tx>; /address/<address>; /token/<address>. Optional",
    valid: v=>(!v || isValidUrl(v)) || "Optionally provide a valid url"
  }
};

export default {
  props: {
    value: Boolean
  },
  data: () => {
    return {
      rules: {
        notNull: (v)=>(v!==undefined && v!==null) || "Cannot accept null",
      },
      variables: {
        // will be filled by the variable definitions
      },
      saved: false,
      configFile: undefined,
    };
  },
  async mounted() {
    await this.getSourceOfVariables();
  },
  computed: {
    asArrayOf() {
      const allKeys = Object.keys(variableDefinitions);
      return allKeys
        .map((key) => ({
          name: key,
          typeof: variableDefinitions[key].typeof,
          order: variableDefinitions[key].order,
          desc: variableDefinitions[key].desc,
          value: this.variables[key],
          valid: variableDefinitions[key].valid,
        }))
        .sort((a, b) => a.order - b.order);
    },
    isConfigValid() {
      for (const v of this.asArrayOf) {
        // if (v.value===undefined || v.value===null) return false;
        if (v.valid(v.value) !== true) return false;
      }
      return true;
    }
  },
  watch: {
    asArrayOf() {
      this.saved = false;
    },
    saved() {
      this.$emit('input', this.isConfigValid && this.saved);
    }, 
    isConfigValid() {
      this.$emit('input', this.isConfigValid && this.saved);
    }
  },
  methods: {
    ...mapActions(["goToPage", "applyVariables"]),
    async getSourceOfVariables() {
      let shouldBeSaved = false;
      let config = undefined;
      // first test the query string for the presence of config-url
      const location = new URL(window.location);
      if (location.searchParams.has("config-url")) {
        try {
          const url = new URL(location.searchParams.get("config-url"));
          const res = await fetch(url.href);
          const content = await res.json();
          location.searchParams.delete("config-url");
          window.history.replaceState(null, "", location.href);
          if (content[storageName]) { // we are loading a file with all attributes not just variables
            config = content[storageName];
            delete content[storageName]; 
            await storage().setAll(content); // save except the variables that will be displayed for validation
          } else { // the file only contains the variables
            config = content;
          }
        } catch (error) {
          console.warn(
            "the config file is not usable",
            location.searchParams.get("config-url"),
            error
          );
        }
      }

      // then look into the local storage
      if (!config) {
        try {
          const content = await storage().getItem(storageName);
          if (!content)
            throw new Error(
              "no configuration available in storage. key = " + storageName
            );
          shouldBeSaved = true;
          config = content;
        } catch (error) {
          console.warn("the config is not in storage", error.message);
        }
      }

      // else create an empty structure
      if (!config) {
        config = {};
      }

      // remove unexpected variables, add missing ones
      const allKeys = Object.keys(variableDefinitions);
      for (const key of Object.keys(config)) {
        if (!allKeys.includes(key)) delete config[key];
      }
      for (const key of allKeys) {
        if (!(key in config)) config[key] = "";
      }

      // set the component prop
      this.variables = config;
      if(shouldBeSaved) await this.saveConfiguration();
    },
    async saveConfiguration() {
      try {
        await storage().setItem(storageName, this.variables);
        await this.applyVariables(this.variables);
        this.saved = true;
      } catch (error) {
        console.error("Fail saving the configuration", error)
      }
    },
    async saveConfigToFile() {
      const content = await storage().getAll();
      const name = (location.origin + "_" + location.pathname).replace(/\//g, "_") + ".json"
      download(name, JSON.stringify(content, null, 2));
    },
    async loadConfigFromFile() {
      console.log("Config file", this.configFile);
      if (!this.configFile) return;
      let content = await this.configFile.text();
      content = JSON.parse(content);
      console.log("content loaded", content);
      await storage().setAll(content)
      await this.getSourceOfVariables()
      // force a reload of the page after loading the config
      window.location.reload();
    }
  },
};
</script>
