<template>
    <v-container>
      <v-layout row wrap>
        <v-simple-table fixed-header >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left text-h6">LOG: block/tx/index Initiator.Event(param1, param2, param3, ...)</th>
              </tr>
            </thead>
            <tbody >
              <tr v-for="log in eventsSorted" :key="log.id" class="pt-6">
                <td>
                  <div>{{log.formatted}} </div>
                  <div class="text--secondary text-body-2">Tx: <expl-a :value="log.transactionHash"></expl-a></div>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
       
      </v-layout>
    </v-container>
</template>

<script>
import { mapActions } from "vuex";
import { formatLog, sortableId } from "../lib/eventlog-formatter";
import ExplA from "./ExplorerLink.vue";

export default {
  components: {ExplA},
  data() {
    return {

    }
  },
  mounted() {
    this.resetEventsCount();
  },

  computed: {
    eventsSorted() {
      return this.$store.getters.events
        .map(log=>({...log, formatted: formatLog(log), sortableId: sortableId(log)}))
        .sort((log1, log2)=>-log1.sortableId.localeCompare(log2.sortableId))
    }
  },
  methods: {
    ...mapActions(["resetEventsCount"]),

  }
}
</script>

