<template>
  <div>
    <v-btn fab elevation="3" top left @click="drawer=!drawer" v-show="!drawer" > <eth-avatar-icon :value="currentAddress"></eth-avatar-icon></v-btn>
  <v-navigation-drawer v-model="drawer" app >
      <wallet-custody-selection :value="activateWalletSelection"></wallet-custody-selection>
      <v-sheet color="grey lighten-4" class="pa-4">
        <v-avatar class="mb-4" color="grey darken-1" size="64" rounded="">
          <eth-avatar-icon :value="currentAddress"></eth-avatar-icon>
        </v-avatar>

        <div v-if="currentAddress">
          <div>{{currentAddress}}</div>
          <div>{{ toNumber(toGWei(walletBalance)) }} GWei</div>
        </div>
        <div v-else><a @click="activateWalletSelection = true">Select a wallet</a></div>
      </v-sheet>

      <v-divider></v-divider>

      <v-list>
        <v-list-item-group :value="value" @change="onChangeEvt($event)" mandatory color="primary">
          <v-list-item v-for="[icon, text, name] in links" :key="icon" link>
            <v-list-item-icon v-if="name=='eventlog' && evtCount>0"> <v-badge :content="evtCount" color="primary"><v-icon>{{ icon }}</v-icon></v-badge> </v-list-item-icon>
            <v-list-item-icon v-else><v-icon>{{ icon }}</v-icon> </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title> {{ text }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import WalletCustodySelection from "./WalletCustodySelection.vue";
import EthAvatarIcon from "./EthAvatarIcon.vue";
import { ethereum } from "../lib/api";
import { ZeroAccount } from '../lib/consts';
import { toGWei, toNumber } from "../lib/numbers";

export default {
  components: {
    WalletCustodySelection,
    EthAvatarIcon,
  },
  props: ['value'],
  data: ()=>({
    drawer: null,
    links: [
      ["mdi-bank", "Bank status", "bank"],
      ["mdi-account-cash", "Client account", "account"],
      ["mdi-wallet", "Wallets", "wallets"],
      ["mdi-radar", "Confirmations", "eventlog"],
      ["mdi-cog", "Configuration", "config"],
    ],
    activateWalletSelection: false,
    walletBalance: "",
  }),
  methods: {
    toGWei, toNumber,
    ...mapActions(["goToPage"]),
    onChangeEvt(ev) {
      //this.$emit('input', this.links[ev][2])
      this.goToPage(this.links[ev][2]);
    },
    async getWalletBalance() {
      console.log("Getting wallet balance for ", this.currentAddress);
      if (this.currentAddress) {
        this.walletBalance = await ethereum().getBalance(this.currentAddress);
      }
    },
  },
  computed: {
    currentAddress() {
      const address = this.$store.getters.currentWalletAddress;
      if (address == ZeroAccount) return null;
      else return address;
    },
    evtCount() {
      return this.$store.getters.newEventsCount;
    },
  },
  watch: {
    currentAddress() {
      this.getWalletBalance();
    }
  }
}
</script>

