import { ZeroAccount } from "./consts";

export function calcEventLogUniqueId(protocol, block, transaction, log) {
  return (
    protocol + ":" +
    String(block).padStart(8, "0") +
    String(transaction).padStart(3, "0") +
    String(log).padStart(3, "0")
  );
}

const mapAddress = new Map();
mapAddr(ZeroAccount, "@Zero");

function low(v) {
  if(v) return v.toLowerCase() 
  return v;
}

export function mapAddr(address, name) {
  mapAddress.set(low(address), name);
}

export function formatLog(prefix, log) {
  if (typeof prefix === "object" && log === undefined) {
    log = prefix;
    prefix = undefined;
  }
  return formatEventLogActual(prefix, log);
}

function formatEventLogActual(prefix, log) {
  if (!log.event) return;
  const params = { ...log.returnValues };
  for (const key of Object.keys(params)) {
    if (Number.isInteger(Number.parseInt(key))) delete params[key];
    if (mapAddress.has(low(params[key]))) params[key] = mapAddress.get(low(params[key]));
  }
  if (mapAddress.has(low(log.address)))
    log.address = mapAddress.get(low(log.address)) || "undefined";
  const paramsTxt = Object.keys(params)
    .map((f) => `${f}:${params[f]}`)
    .join(", ");
  return `LOG: ${prefix ? prefix + ": " : ""}${log.blockNumber}/${
    log.transactionIndex
  }/${log.logIndex} ${log.address}.${log.event}(${paramsTxt})`;
}

export function sortableId(log) {
  if(!log) return "";
  return calcEventLogUniqueId("eth", log.blockNumber, log.transactionIndex, log.logIndex)
}