<template>
  <v-container>
    <v-overlay :value="transactionInProgress">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
    <v-layout row wrap>
      <v-row>
        <v-col cols="12">
          <v-card outlined elevation="3" class="pa-3"> 
            <v-row>
              <v-col cols="3" align-self="center">
                <v-btn color="primary" @click="createNewAccount">Create new account</v-btn>
              </v-col>
              <v-col cols="9" align-self="center">
                <v-text-field
                  label="New account name"
                  counter
                  v-model="newAccountName"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
        <v-col cols="12" v-for="account in accounts" :key="account.address">
          <v-card class="pa-3" 
            @click="selectedAccount=account.address"
            :elevation="selectedAccount==account.address?'12':'2'"
            :outlined="selectedAccount==account.address?true:false"
          > 
            <v-row>
              <v-col cols="2" align-self="center">
                <div>{{account.name}}</div>
                <div>{{formatting.format(account.balance)}}</div>
              </v-col>
              <v-col cols="5" align-self="center">
                <div v-if="selectedAccount==account.address">
                  <v-text-field label="amount" type="number" v-model="changeBalanceAmount"></v-text-field>
                  <v-btn @click="changeBalance(1)" v-if="!showTransfer">credit</v-btn>
                  <v-btn @click="changeBalance(-1)" v-if="!showTransfer">debit</v-btn>
                  <v-btn @click="showTransfer=true" v-if="!showTransfer">Transfer ...</v-btn>
                  <v-text-field label="target account address" type="text" v-model="transferToAddress" v-if="showTransfer"></v-text-field>
                  <v-btn @click="showTransfer=false" v-if="showTransfer">Cancel</v-btn>
                  <v-btn @click="executeTransfer" v-if="showTransfer">Ok</v-btn>
                </div>
              </v-col>
              <v-col cols="5" align-self="center">
                <div>address: <expl-a :value="account.address" type="token"></expl-a></div>
                <div>IBAN: {{account.iban}}</div>
                <div>owner: <expl-a :value="account.owner"></expl-a></div>
                <v-row v-if="selectedAccount==account.address">
                  <v-col cols="9" align-self="center">
                    <v-text-field messages="Whitelist a wallet to grant access"  label="grant access to" type="text" v-model="whitelistedAddress" ></v-text-field>
                  </v-col>
                  <v-col cols="3" align-self="center">
                    <v-btn @click="whitelistAddress" >Ok</v-btn>
                  </v-col>
                </v-row>
              </v-col>

                
            </v-row>
          </v-card>
        </v-col>
      </v-row>
    </v-layout>
    <div class="mt-8">*) Note on Iban: XE country code is not widely recognized by the banking community. Alternative encoding may be needed, such as having the bank smart contract provide a 2 letter prefix instead</div>
  </v-container>
</template>

<script>
import { connectToNode, storage, bankContract, accountContract, toHex, ensureFlag, Iban, storageNames } from "../lib/api";
import { mapAddr } from '../lib/eventlog-formatter';
import { notifySuccess, walletAuthDialog } from '../store';
import ExplA from "./ExplorerLink.vue";

const accountAddressesName = storageNames.accountAddresses;
export default {
  components: {ExplA},
  data(){
    return {
      transactionInProgress: false,
      currency: "",
      decimals: 0,
      accountAddresses: [],
      accounts: [],
      newAccountName: "",
      selectedAccount: "",
      changeBalanceAmount: null,
      formatting: new Intl.NumberFormat('en', {minimumFractionDigits: 2}),
      showTransfer: false,
      transferToAddress: undefined,
      whitelistedAddress: undefined,
    }
  },
  async mounted() {
    
    const l = await storage().getItem(accountAddressesName);
    if (l && Array.isArray(l)) {
      this.accountAddresses = l;
      this.accounts = l.filter(a=>!!a).map(a=>({
        address: a,
        name: "",
        balance: undefined,
        owner: undefined,
        iban: undefined
      }));

      await this.getCurrency();
      this.formatting = new Intl.NumberFormat('en', {minimumFractionDigits: this.decimals , style: this.currency?"currency":undefined, currency: this.currency || undefined});

      this.fillAccounts();
    }
  },
  computed: {
    currentAddress() {
      return this.$store.getters.currentWalletAddress;
    },
    bankInstance() {
      if (this.$store.getters.bankSmartContract) 
        return bankContract().at(this.$store.getters.bankSmartContract);
      else 
        return undefined;
    }
  },
  methods: {
    async getCurrency() {
      if (!this.currentAddress || !this.bankInstance) return;
      const intf = connectToNode(this.currentAddress, walletAuthDialog);
      this.currency = await this.bankInstance.symbol(intf.call());
      this.decimals = Number.parseInt(await this.bankInstance.decimals(intf.call()));
      console.log("Currency: ", this.currency, this.decimals)
    },
    async saveAddresses() {
      await storage().setItem(accountAddressesName, this.accountAddresses);
    },
    async fillAccounts(onlyAddress) {
      if (!this.currentAddress) return;
      const intf = connectToNode(this.currentAddress, walletAuthDialog);
      const arrayToProcess = onlyAddress? [onlyAddress] : this.accountAddresses;
      const arrayResult = [];
      for (const address of this.accountAddresses) {
        if (arrayToProcess.includes(address) ) {
          const account = {address};
          const inst = accountContract().at(account.address);
          account.name = await inst.getAttributeStr(intf.call(), toHex("name"));
          console.log("Actual balance value", await inst.balance(intf.call()))
          account.balance = Number.parseFloat(await inst.balance(intf.call())) / 10**this.decimals;
          account.owner = await inst.owner(intf.call());
          account.iban = Iban.from(account.address);
          mapAddr(account.address, account.name);
          arrayResult.push(account);
        } else {
          arrayResult.push(this.accounts.find(a=>a.address === address));
        }
      }

      this.accounts = arrayResult;
    },
    async createNewAccount() {
      if (!this.currentAddress || !this.bankInstance) return;
      if (!this.newAccountName) return;

      await ensureFlag(this, 'transactionInProgress', async ()=>{
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const inst = await accountContract().deploy(intf.newi({maxGas: 1200000}), this.newAccountName);
        await inst.transferOwnership(intf.send({maxGas:50000}), this.bankInstance.deployedAt);
        await this.bankInstance.registerAccount(intf.send({maxGas: 100000}), inst.deployedAt);
        notifySuccess(`New account created.`)
        this.accountAddresses = [...this.accountAddresses, inst.deployedAt];
        await this.saveAddresses();
        await this.fillAccounts(inst.deployedAt);
      })

    },
    async changeBalance(sign) {
      if (!this.currentAddress || !this.bankInstance) return;
      if (!this.selectedAccount) return;
      if (!this.changeBalanceAmount) return;
      await ensureFlag(this, 'transactionInProgress', async ()=>{
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const amount = Math.round(this.changeBalanceAmount * 10**this.decimals)
        if(sign > 0) {
          await this.bankInstance.credit(intf.send({maxGas:100000}), this.selectedAccount, amount);
          notifySuccess(`Account credited with ${this.changeBalanceAmount} ${this.currency}`)
        } else {
          await this.bankInstance.debit(intf.send({maxGas:100000}), this.selectedAccount, amount)
          notifySuccess(`Account debited with ${this.changeBalanceAmount} ${this.currency}`)
        }
        this.changeBalanceAmount = undefined;
        await this.fillAccounts(this.selectedAccount);
      })
    },
    async executeTransfer() {
      if (!this.currentAddress || !this.bankInstance) return;
      if (!this.selectedAccount) return;
      if (!this.changeBalanceAmount) return;
      if (!this.transferToAddress) return;
      await ensureFlag(this, 'transactionInProgress', async ()=>{
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const amount = Math.round(this.changeBalanceAmount * 10**this.decimals)
        const account = accountContract().at(this.selectedAccount);
        await account.transfer(intf.send({maxGas: 300000}), this.transferToAddress, amount);
        notifySuccess(`Transfer ${this.changeBalanceAmount} ${this.currency} to account.`)
        await this.fillAccounts(this.selectedAccount);
        await this.fillAccounts(this.transferToAddress);
        this.changeBalanceAmount = undefined;
        this.transferToAddress = undefined;
      })
    },
    async whitelistAddress() {
      if (!this.currentAddress ) return;
      if (!this.selectedAccount) return;
      if (!this.whitelistedAddress) return;
      await ensureFlag(this, 'transactionInProgress', async ()=>{
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const account = accountContract().at(this.selectedAccount);
        await account.whitelist(intf.send({maxGas: 50000}), this.whitelistedAddress);
        notifySuccess(`Wallet whitelisted for the account.`)
        this.whitelistedAddress = undefined;
      })
    }
  }
}
</script>

