import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import { setAccessToken, initApis, storage, bankContract, connectToNode, iou, storageNames } from "./lib/api";
import { refreshAccessToken, silentRelogin, popupLogin } from "./lib/oauth2";

//let KeepReceiver;
const store = new Vuex.Store({
  state: {
    userIdentity: null,
    serviceToken: "service-token",
    oauth2Tokens: null,
    tokenRefreshTimer: null,
    activePage: "",

    walletPasswordDialog: undefined,
    snackbars: undefined,
    variables: {},
    currentWalletAddress: null,
    bankSmartContract: undefined,
    bankEventsCollectedFor: undefined,
    events: [],
    newEventsCount: 0
  },
  mutations: {
    setUserIdentity(state, identity) {
      state.userIdentity = identity;
    },
    setActivePage(state, page) {
      state.activePage = page;
    },
    setOauth2Tokens(state, tokens) {
      state.serviceToken = tokens.access_token;
      state.oauth2Tokens = tokens;
    },
    setTokenRefreshTimer(state, timer) {
      // ensure there is a single timer at any time
      if (state.tokenRefreshTimer) clearTimeout(state.tokenRefreshTimer);
      state.tokenRefreshTimer = timer;
    },
    setVariables(state, variables) {
      state.variables = variables;
    },
    setCurrentWalletAddress(state, address) {
      state.currentWalletAddress = address;
    },
    setWalletPasswordDialog(state, instance) {
      state.walletPasswordDialog = instance;
    },
    setSnackbar(state, instance) {
      if ("addError" in instance && "addConfirm" in instance) {
        state.snackbars = instance;
      }
    },
    setBankSmartContract(state, address) {
      state.bankSmartContract = address;
    },
    setBankEventsCollectedFor(state, struct) {
      state.bankEventsCollectedFor = struct;
    },
    addEvent(state, event) {
      if (event.error) {
        state.events.push(event);
      } else {
        // dedup events
        const found = state.events.find(l=>l.blockNumber==event.blockNumber && l.transactionIndex==event.transactionIndex && l.logIndex==event.logIndex)
        if (!found) {
          state.events.push(event);
          state.newEventsCount = state.newEventsCount +1;
        }
      }
    },
    setEventsCount(state, value) {
      state.newEventsCount = value;
    }
  },
  actions: {
    async applyUserIdentity({ commit }, identity) {
      commit("setUserIdentity", identity);
    },
    async applyUserOauth2Tokens({ commit, dispatch }, tokens) {
      setAccessToken(tokens.access_token);
      commit("setOauth2Tokens", tokens);

      const timer = setTimeout(async () => {
        await dispatch("refreshAccessToken");
      }, (tokens.expires_in - 10) * 1000);
      commit("setTokenRefreshTimer", timer);
    },
    async refreshAccessToken({state, dispatch}, redirect = true) {
      const tokens = state.oauth2Tokens;
      if (!tokens) return;
      const refreshToken = tokens.refresh_token;
      if (!refreshToken) return;
      let newTokens;
      try {
        newTokens = await refreshAccessToken(refreshToken);
        console.log("Token refreshed", newTokens);
      } catch (error) {
        console.warn(
          "Fail refreshing the authentication. Trying iframe silent login",
          error
        );
        try {
          newTokens = await silentRelogin(tokens.origin_issuer);
          console.log("Token re login", newTokens);
        } catch (error) {
          console.warn(
            "Fail silently updating the authentication. Trying popup login",
            error
          );
          try {
            newTokens = await popupLogin(tokens.origin_issuer);
            console.log("Token popup login", newTokens);
          } catch (error) {
            console.error(
              "Fail updating the authentication in a popup",
              error
            );
          }
        }
      }
      if (newTokens) {
        newTokens.refresh_token = refreshToken;
        await dispatch("applyUserOauth2Tokens", newTokens);
      } else {
        if (redirect) {
          window.location = window.location.href;
        } else {
          throw new Error("Fail to connect to the IDP");
        }
      }
    },
    async goToPage({ commit, state }, page) {
      if (state.activePage !== page) {
        commit("setActivePage", page);
      }
    },
    async applyVariables({commit}, variables) {
      await initApis(variables);
      commit("setVariables", variables);
    },
    async applyCurrentWalletAddress({commit}, address) {
      commit("setCurrentWalletAddress", address);
    },
    async applyWalletPasswordDialog({commit}, instance) {
      commit("setWalletPasswordDialog", instance);
    },
    async applySnackbars({commit}, instance) {
      commit("setSnackbar", instance);
    },
    async authenticateWallet({state}, { api, address}) {
      const el = state.walletPasswordDialog
      if (!el) throw new Error("Ensure the <wallet-password> element is configured ");
      let token = undefined;
      let errorMessage = undefined
      let ret = {address, ok: true, password: undefined, keepPassword: false};
      // try first to get the password from the session storage
      ret.password = window.sessionStorage.getItem("pwd-"+address);
      while (!token ) {
        // if not then get the password from the user interface
        if(!ret.password) {
          ret = await el.showDialog(address, errorMessage);
        }
        console.log("returned", address, ret, token, errorMessage);
        // console.log("returned", ret);
        if (!ret.ok) { // cancel clicked
          return undefined;
        }
        try {
          token = await api.authenticate(address, ret.password)

          // if the authentication succeded then keep the password if requested
          if (ret.keepPassword) {
            window.sessionStorage.setItem("pwd-"+address, ret.password)
          }
        } catch (error) {
          // if authentication failed, forget the saved password
          // window.sessionStorage.removeItem("pwd-"+address);
          ret.password = undefined;
          console.log("failed", error);
          errorMessage = error.message;
        }
      }
      console.log("Returning token", token)
      return token;
    },
    async intitializeFromStorage({commit}) {
      const bank = await storage().getItem(storageNames.bankAddress);
      commit("setBankSmartContract", bank);
    },
    async saveBankSmartContract({commit}, address) {
      commit("setBankSmartContract", address);
      await storage().setItem(storageNames.bankAddress, address);
    },
    async findBankAccounts({state}) {
      if (!this.state.bankSmartContract) return;
      const bank = bankContract().at(state.bankSmartContract);
      const intf = connectToNode(state.bankSmartContract, ()=>Promise.resolve(""));
      
      const events = await new Promise((resolve, reject)=> {
        const logs = [];
        bank.events.AccountStatus(intf.get({fromBlock: 0}))
          .on("log", log=>logs.push(log))
          .on("error", reject)
          .on("completed", ()=>resolve(logs));
      });
      const accounts = events.map(e=>e.returnValues.account);
      const existing = await storage().getItem(storageNames.accountAddresses)||[];
      for (const account of accounts) {
        if (!existing.includes(account)) existing.push(account);
      }
      await storage().setItem(storageNames.accountAddresses, existing);
    },
    async findLinkedBanks({state}) {
      if (!this.state.bankSmartContract) return;
      const bank = bankContract().at(state.bankSmartContract);
      const intf = connectToNode(state.bankSmartContract, ()=>Promise.resolve(""));
      
      const events = await new Promise((resolve, reject)=> {
        const logs = [];
        bank.events.BankLinkStatus(intf.get({fromBlock: 0}))
          .on("log", log=>logs.push(log))
          .on("error", reject)
          .on("completed", ()=>resolve(logs));
      });
      const banks = events.map(e=>e.returnValues.bank);
      const existing = await storage().getItem(storageNames.registeredBankAddresses)||[];
      for (const bank of banks) {
        if (!existing.includes(bank)) existing.push(bank);
      }
      await storage().setItem(storageNames.registeredBankAddresses, existing);
    },
    async collectBankEvents({commit, state}, {subscribe, fromBlock}) {
      if ( state.bankEventsCollectedFor && state.bankEventsCollectedFor.address == state.bankSmartContract) return; // only one registration
      console.log("ready so subscribe", subscribe, fromBlock);
      const bank = bankContract().at(state.bankSmartContract);
      // the last 'true' parameter is used to force the creation of a new web3 instance to avoid the subscription to loose its id
      const intf = connectToNode(state.bankSmartContract, ()=>Promise.resolve(""), true);
      const func = subscribe ? intf.sub({fromBlock}) : intf.get({fromBlock});

      const bankReceiver = bank.allEvents(func);
      bankReceiver.on("log", log=>{
        // console.log("Log received bank", log);
        commit("addEvent", log)})
      bankReceiver.on("error", err=>commit("addEvent", {error:err}));

      const iouReceiver = iou().allEvents(func);
      iouReceiver.on("log", log=>{
        // console.log("Log received IOU", log);
        commit("addEvent", log)})
      iouReceiver.on("error", err=>commit("addEvent", {error:err}));

      commit("setBankEventsCollectedFor", {address: state.bankSmartContract, bankReceiver, iouReceiver});
    },
    async resetEventsCount({commit}) {
      commit("setEventsCount", 0);
    }
  },
  getters: {
    activePage(state) {
      return state.activePage;
    },
    userIdentity(state) {
      if (state.userIdentity) {
        return {
          email: state.userIdentity.email,
          name: state.userIdentity.name,
        };
      } else
        return {
          email: "",
          name: "",
        };
    },
    userAccessToken(state) {
      return state.serviceToken;
    },
    authenticated(state) {
      return !!state.oauth2Tokens;
    },
    variables(state) {
      return {...state.variables};
    },
    currentWalletAddress(state) {
      return state.currentWalletAddress;
    },
    displayWalletPasswordDialog(state) {
      return state.displayWalletPasswordDialog;
    },
    bankSmartContract(state) {
      return state.bankSmartContract;
    },
    events(state) {
      return state.events;
    },
    newEventsCount(state) {
      return state.newEventsCount;
    }
  },
});
export default store;

async function inits() {
  await store.dispatch("intitializeFromStorage")
}
inits();

export async function walletAuthDialog(address, api) {
  return await store.dispatch("authenticateWallet", {api, address})
}

export function notifyError(message) {
  store.state.snackbars.addError(message)
} 
export function notifySuccess(message) {
  store.state.snackbars.addConfirm(message)
} 