<template>
  <v-dialog v-model="displayAuthenticationDialog" persistent max-width="650px">
    <v-card >
      <v-card-title>
        <v-row>
          <v-col cols="2" align-self="center">
            <eth-avatar-icon :value="walletAddress"></eth-avatar-icon>
          </v-col>
          <v-col cols="10" align-self="center">
            <div class="text-h5">Sign transaction with</div>
            <div class="text-h6">{{ walletAddress }}</div>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col>
              <v-text-field
                v-model="password"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :type="showPassword ? 'text' : 'password'"
                name="password"
                label="Enter the wallet password"
                counter
                autofocus
                @click:append="showPassword = !showPassword"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row v-if="!!authenticationError">
            <v-col>
              <span class="red--text text-h6">{{ authenticationError }}</span>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-checkbox label="Remember my password for the session" v-model="rememberPassword"></v-checkbox>
        <v-spacer></v-spacer>
        <v-btn ref="close"  color="blue darken-1" text @click="closeAuthenticationDialog()">
          Close
        </v-btn>
        <v-btn ref="verify" color="primary darken-1" text @click="verifyAuthentication()">
          Verify
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import EthAvatarIcon from "./EthAvatarIcon.vue";
export default {
  components: {EthAvatarIcon},
  data() {
    return {
      password: undefined,
      walletAddress: undefined,
      displayAuthenticationDialog: false,
      showPassword: false,
      authenticationError: undefined,
      rememberPassword: false
    }
  },
  methods: {
    async closeAuthenticationDialog() {
      // await this.completeAndEmitEvent(false);

    },
    async verifyAuthentication() {
      // await this.completeAndEmitEvent(true);
    },
    async showDialog(address, errorMessage) {
      // control if there is a concurrent use of the dialog with 2 different address
      if (this.displayAuthenticationDialog && this.walletAddress !== address) {
        throw new Error("The dialog cannot be used twice with 2 different addresses at the same time")
      }
      // otherwise the code ca be re executed on the same instance
      // double listener to return the password on ok / cancel
      if (!this.displayAuthenticationDialog) {
        this.displayAuthenticationDialog = true;
        this.authenticationError = errorMessage;
        this.walletAddress = address;
      }
      // important : ensures that the DOM is ready and the refs are available
      await this.$nextTick();
      const ret = await new Promise((resolve)=>{
        this.$refs.verify.$once("click", ()=>{resolve({address, password: this.password, keepPassword: this.rememberPassword, ok: true})})
        this.$refs.close.$once("click", ()=>{resolve({address, ok: false})})
      })
      this.displayAuthenticationDialog = false;
      this.password = undefined
      this.authenticationError = undefined;
      this.walletAddress = undefined;
      return ret;
    }
  }
};
</script>

