<template>
  <v-container>
    <v-overlay :value="transactionInProgress">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
    <v-layout row wrap>
      <v-row>
        <v-col cols="12">
          <h2>This Bank : {{ currency }} </h2>with {{ decimals }} decimals
        </v-col>
        <v-col cols="12" v-if="!bankInstance">
          <div>The instance of the bank does not exists or is unknown.</div>
          <div>If you have the address paste it here :</div>
          <v-row>
            <v-col cols="11" align-self="center">
              <v-text-field
                label="bank smart contract"
                append-icon="mdi-close"
                v-model="capturedBankAddress"
                @click:append="capturedBankAddress = ''"
              ></v-text-field>
            </v-col>
            <v-col cols="1" align-self="center">
              <v-btn
                color="secondary"
                small
                @click="saveBankSmartContract(capturedBankAddress)"
                >Ok</v-btn
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6" align-self="center">
              If you do not have a bank instance in the blockchain create it now
            </v-col>
            <v-col cols="6" align-self="center">
              <v-btn color="primary" small @click="createBankInstance"
                >Create the bank</v-btn
              >
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" v-else>
          <v-row>
            <v-col cols="12" >
              <div>Bank instance: <expl-a :value="bankInstance.deployedAt" type="token"></expl-a></div>
              <div>Bank instance owner: <expl-a :value="owner"></expl-a></div>
            </v-col>
            <v-col cols="12" >
              Total commercial money supply in circulation:
              <span >{{ formatting.format(totalSupply) }}</span>
            </v-col>
            <v-col cols="12" >
              Total debt against other banks:
              <span :class="globalDebt<0?'red--text':'green--text'">{{ formatting.format(globalDebt) }}</span>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12">
          <v-row>
            <v-col cols="12"><h2>List of registered banks</h2>that you trust with debt you will accept on them</v-col>
            <v-col
              cols="12"
              v-for="bank in registeredBankDetails"
              :key="bank.address"
            >
              <v-card outlined elevation="3" class="pa-4">
                <v-card-title>Bank  &nbsp;<expl-a :value="bank.address" type="token"></expl-a></v-card-title>
                <div>Owner: <expl-a :value="bank.owner"></expl-a></div>
                <v-row>
                  <v-col cols="6">
                    <div>Status: {{ bank.registered?"Registered":"Not registered" }}</div>
                    <div>We owe them: {{ formatting.format(bank.debtFromUs) }}</div>
                    <div>They owe us: {{ formatting.format(bank.debtToUs) }}</div>
                  </v-col>
                  <v-col cols="6" align-self="end" v-if="bank.debtToUs>0">
                    <v-btn @click="confirmInterbankRepayment(bank.address, bank.debtToUs)">Confirm you received the payment of {{formatting.format(bank.debtToUs)}}</v-btn>
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
            <v-col cols="12">
              <v-card outlined elevation="3" class="pa-4">
                <v-card-title>Register a new bank</v-card-title>
                <v-row>
                  <v-col cols="11" align-self="center">
                    <v-text-field
                      label="bank smart contract address"
                      append-icon="mdi-close"
                      v-model="capturedBankAddress"
                      @click:append="capturedBankAddress = ''"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="1" align-self="center">
                    <v-btn
                      color="secondary"
                      small
                      @click="addRegisteredBank(capturedBankAddress)"
                      >Ok</v-btn
                    >
                  </v-col>
                </v-row>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import {
  ethereum,
  connectToNode,
  iou,
  bankContract,
  storage,
  ensureFlag,
  storageNames,
} from "../lib/api";
import { mapActions } from "vuex";
import { notifySuccess, walletAuthDialog } from "../store";
import ExplA from "./ExplorerLink.vue";

const registeredBankName = storageNames.registeredBankAddresses;

export default {
  components: {ExplA},
  data() {
    return {
      transactionInProgress: false,
      walletBalance: "",
      currency: "",
      decimals: 0,
      owner: "",
      capturedBankAddress: "",
      registeredBanks: [],
      registeredBankDetails: [],
      globalDebt: 0,
      totalSupply: 0,
      formatting: new Intl.NumberFormat('en', {minimumFractionDigits: 2}),
    };
  },
  async mounted() {
    await this.$nextTick() // needed to delay the variables initializtion after the loading of the page
    this.registeredBanks = await storage().getItem(registeredBankName) || [];
    try {
      await this.getBankCurrencyFromIOU();
      await this.getIOUGlobalDebt();
      await this.getBankDetails();
      await this.getRegisteredBankDetails();
    } catch (error) {
      console.error("Failed initializing", error)
    }
    this.formatting = new Intl.NumberFormat('en', {minimumFractionDigits: this.decimals , style: this.currency?"currency":undefined, currency: this.currency || undefined});
  },
  computed: {
    currentAddress() {
      return this.$store.getters.currentWalletAddress;
    },
    bankInstance() {
      if (this.$store.getters.bankSmartContract)
        return bankContract().at(this.$store.getters.bankSmartContract);
      else return undefined;
    },
  },
  watch: {
    async currentAddress() {
      await this.getWalletBalance();
    },
    bankInstance(v) {
      if (v) {
        this.getIOUGlobalDebt();
      }
    },
  },
  methods: {
    ...mapActions(["authenticateWallet", "saveBankSmartContract"]),

    async getWalletBalance() {
      console.log("Getting wallet balance for ", this.currentAddress);
      if (this.currentAddress) {
        this.walletBalance = await ethereum().getBalance(this.currentAddress);
      }
    },
    async getBankCurrencyFromIOU() {
      if (!this.currentAddress) return;
      const intf = connectToNode(this.currentAddress, walletAuthDialog);
      this.currency = await iou().symbol(intf.call());
      this.decimals = Number.parseInt(await iou().decimals(intf.call()));
    },

    async createBankInstance() {
      if (!this.currentAddress) return;
      if (this.bankInstance) return; // do not create it twice
      await ensureFlag(this, 'transactionInProgress', async ()=>{
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const inst = await bankContract().deploy(
          intf.newi({ maxGas: 3000000 }),
          this.currency,
          this.decimals,
          iou().deployedAt
        );
        await this.saveBankSmartContract(inst.deployedAt);
        notifySuccess("The bank smart contract has beed created.")
      })
    },
    async getBankDetails() {
      if (this.bankInstance && this.currentAddress) {
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        this.owner = await this.bankInstance.owner(intf.call());
        this.totalSupply = Number.parseFloat(await this.bankInstance.totalSupply(intf.call()));
        this.totalSupply = this.totalSupply / 10**this.decimals;
        console.log("Bank owner", this.owner);
      }
    },
    async getIOUGlobalDebt() {
      if (this.bankInstance && this.currentAddress) {
        const intf = connectToNode(this.currentAddress, walletAuthDialog);
        const unsigned = await iou().balanceOf(
          intf.call(),
          this.bankInstance.deployedAt
        );
        const sign = await iou().balanceSignOf(
          intf.call(),
          this.bankInstance.deployedAt
        );
        console.log("getIOUGlobalDebt", sign, unsigned);
        this.globalDebt =
          (Number.parseInt(sign) * Number.parseInt(unsigned)) /
          10 ** this.decimals;
      }
    },
    async getRegisteredBankDetails() {
      if (this.bankInstance && this.currentAddress) {
        const intf = connectToNode(this.currentAddress, walletAuthDialog);

        const arrayResult = [];
        for (const bank of this.registeredBanks) {
          const detail = { address: bank };
          const their = bankContract().at(bank);
          detail.owner = await their.owner(intf.call());
          detail.registered = await this.bankInstance.isBankRegistered(intf.call(), bank);
          detail.debtFromUs = Number.parseFloat(
            await iou().bankOwesTo(
              intf.call(),
              this.bankInstance.deployedAt,
              bank
            )
          );
          detail.debtToUs = Number.parseFloat(
            await iou().bankOwesTo(
              intf.call(),
              bank,
              this.bankInstance.deployedAt
            )
          );
          detail.debtFromUs = detail.debtFromUs / 10**this.decimals
          detail.debtToUs = detail.debtToUs / 10**this.decimals
          arrayResult.push(detail);
        }
        this.registeredBankDetails = arrayResult;
      }
    },
    async addRegisteredBank(address) {
      if (this.bankInstance && this.currentAddress) {
        await ensureFlag(this, 'transactionInProgress', async ()=>{
          const intf = connectToNode(this.currentAddress, walletAuthDialog);
          const ownerToCheck = await bankContract().at(address).owner(intf.call());
          if (!ownerToCheck) throw new Error("The address of the smart contract is not a valid bank address");
          await this.bankInstance.registerBank(intf.send({maxGas:100000}), address);
          this.registeredBanks.push(address);
          await storage().setItem(registeredBankName, this.registeredBanks);
          await this.getRegisteredBankDetails();
          notifySuccess("The external bank has been registered.")
          this.capturedBankAddress = undefined;
        })
      }
    },
    async confirmInterbankRepayment(bank, debtToUs){
      if (this.bankInstance && this.currentAddress) {
        await ensureFlag(this, 'transactionInProgress', async ()=>{
          const intf = connectToNode(this.currentAddress, walletAuthDialog);
          await this.bankInstance.confirmInterbankPayment(intf.send({maxGas:100000}), bank, debtToUs * 10**this.decimals);
          notifySuccess(`The debt of ${debtToUs} ${this.currency} has been cleared.`)
          await this.getRegisteredBankDetails();
          await this.getIOUGlobalDebt();
        })
      }
    }
  },
};
</script>

<style></style>
