<template>
  <v-app id="inspire">
    <wallet-password ref="unique-wallet-password-id"> </wallet-password>
    <snackbars ref="snackbars"></snackbars>
    <!-- <v-system-bar app>
      <v-spacer></v-spacer>

      <v-icon>mdi-square</v-icon>

      <v-icon>mdi-circle</v-icon>

      <v-icon>mdi-triangle</v-icon>
    </v-system-bar> -->

    <!-- <login v-if="!loggedIn"></login> -->
    <div v-if="!configurationOk">
      <v-main>
        <v-container class="py-8 px-6" fluid>
          <configuration v-model="configurationOk"></configuration>
        </v-container>
      </v-main>
    </div>
    <div v-else>
      <app-menu></app-menu>

      <v-main>
        <v-container class="py-8 px-6" fluid>
          <!-- <dashboard v-if="selectedItem == 'dashboard'"></dashboard> -->
          <bank-page v-if="selectedItem == 'bank'"></bank-page>
          <accounts-page v-if="selectedItem == 'account'"></accounts-page>
          <configuration v-if="selectedItem == 'config'"></configuration>
          <wallets-page v-if="selectedItem == 'wallets'"></wallets-page>
          <event-logs v-if="selectedItem == 'eventlog'"></event-logs>
        </v-container>
      </v-main>
    </div>
  </v-app>
</template>

<script>
import AppMenu from "./components/AppMenu.vue";
// import Login from "./components/Login.vue";
import Configuration from "./components/Configuration.vue";
import BankPage from "./components/BankPage.vue";
import AccountsPage from "./components/AccountsPage.vue";
import WalletPassword from "./components/WalletPassword.vue";
import WalletsPage from "./components/WalletsPage.vue";
import EventLogs from "./components/EventLogs.vue";
import Snackbars from "./components/Snackbars.vue";

import { mapActions } from "vuex";
import { mapAddr } from './lib/eventlog-formatter';
import { notifyError } from './store';
export default {
  components: { 
    AppMenu, 
    // Login, 
    Configuration,
    BankPage,
    AccountsPage,
    EventLogs,
    WalletsPage,
    WalletPassword,
    Snackbars
     },
  data: () => ({
    configurationOk: false,
    pollingInError: false,
    lastErrorMessage: "",
  }),
  computed: {
    selectedItem() {
      return this.$store.getters.activePage;
    },
    loggedIn() {
      // use the below line to enable external IDP authentication with the Login.vue component
      return true; // and remove this one
      //return !!this.$store.getters.authenticated;
    },
    initializationReady() {
      const bankSmartContractReady = !!this.$store.getters.bankSmartContract;
      const variablesReady = !!this.$store.getters.variables.IOUAddress;
      return variablesReady && bankSmartContractReady
    }
  },
  watch: {
    initializationReady(n, o) {
      console.log("watched initializationReady", n, o);
      mapAddr(this.$store.getters.bankSmartContract, "ThisBank");
      this.collectBankEvents({subscribe: true, fromBlock: 0});
      this.findBankAccounts();
      this.findLinkedBanks();
    }
  },
  methods: {
    ...mapActions(["loadData", "refreshAccessToken", "applyWalletPasswordDialog", "applySnackbars", "collectBankEvents", "findBankAccounts", "findLinkedBanks"]),
  },
  async mounted() {
    console.log("applyWalletPasswordDialog", this.$refs["unique-wallet-password-id"])
    await this.applyWalletPasswordDialog(this.$refs["unique-wallet-password-id"]);
    await this.applySnackbars(this.$refs["snackbars"]);
  },
  errorCaptured(err) {
    notifyError(err.message || err)
    return false;
  }
};
</script>
