import { WalletCustodyApiImpl } from "@saturn-chain/wallet-custody-rest-api";
import {  } from "@saturn-chain/dlt-tx-data-functions";
import {  } from "@saturn-chain/smart-contract";
import { Web3CustodyFunctionProvider } from "@saturn-chain/web3-custody-functions";
import CashBankContracts from "sc-bank-account";

import Web3 from "web3";
import { AsyncLocalStorage } from "./async-local-storage";
import { mapAddr } from "./eventlog-formatter";
// import { toNumber, toGWei, toWei } from "./numbers";

let currentAccessToken;
export function setAccessToken(token) {
  currentAccessToken = token;
  console.log("To be removed when currentAccessToken is used", currentAccessToken);
}


export const storageNames = {
  walletAddresses: "bank-visited-wallets",
  bankAddress: "bank-smart-contract-address",
  accountAddresses: "bank-account-addresses",
  registeredBankAddresses:  "bank-linked-banks-registered",
  mainConfig: "application-configuration-variables",
}

const _localStorage = new AsyncLocalStorage(Object.values(storageNames));
export function storage() {
  return _localStorage;
}


let initialized = false;
let variables = undefined;
let web3 = new Web3();
let custodyApi = new WalletCustodyApiImpl("http://pseudo", "eth");
let IOUContract = CashBankContracts.get("IOU");
let iouInstance = IOUContract.at("");
let BankContract = CashBankContracts.get("CommercialBankCash");
let AccountContract = CashBankContracts.get("BankAccount");

export async function initApis(_variables) {
  variables = _variables;
  console.log("Initializing api with variables", {...variables});
  const ws = new Web3.providers.WebsocketProvider(variables.web3WebSocketUrl, {reconnect: {auto: true, delay:400, onTimeout: true}})
  web3 = new Web3(ws);
  custodyApi = new WalletCustodyApiImpl(variables.walletCustodyService, "eth");
  iouInstance = IOUContract.at(variables.IOUAddress);
  mapAddr(iouInstance.deployedAt, "IOU");
  const chainId = await web3.eth.getChainId();
  console.log("Connected to chain", chainId);
  initialized = true;
}

function requireInitialized() {
  if (!initialized) throw new Error("Use the initApis() function first");
}

export function connectToNode(address, asyncAuthFunction, newWeb3 = false) {
  requireInitialized();
  let _web3 = web3;
  // important node: the newWeb3 is a flg to force the creation of a new Web3 instance.
  // this is important for situations where the json-rpc id is duplicated because two requests gets submitted at the same time
  // So one of the stream is better having a second instance ! 
  if (newWeb3) {
    const ws = new Web3.providers.WebsocketProvider(variables.web3WebSocketUrl, {reconnect: {auto: true, delay:400, onTimeout: true}})
    _web3 = new Web3(ws);
  }
  if (! _web3.utils.isAddress(address)) throw new Error("Provide a valid Ethereum address");
  if (typeof asyncAuthFunction !== "function") throw new Error("Expect an asynchronous function to ba called to authenticate the wallet");
  const intf = new Web3CustodyFunctionProvider(
    _web3.currentProvider,
    custodyApi,
    address,
    asyncAuthFunction
  )
  return intf;
}

export function ethereum() {
  requireInitialized();
  
  return web3.eth;
}
export function toHex(v) {
  requireInitialized();
  return web3.utils.toHex(v);
}
export const Iban = {
  from: (address)=>web3.eth.Iban.toIban(address),
  to: (iban)=>web3.eth.Iban.toAddress(iban),
} 

export function custody() {
  requireInitialized();
  return custodyApi;
}

export function iou() {
  requireInitialized();
  return iouInstance;
}

export function bankContract() {
  return BankContract;
}

export function accountContract() {
  return AccountContract;
}

export async function ensureFlag(component, flagName, execFunc) {
  try {
    component[flagName] = true;
    await execFunc()
  } finally {
    component[flagName] = false;
  }
}