<template>
  <div>
    <v-snackbar 
      v-for="(bar) in bars" :key="bar.id" 
      v-model="bar.visible"
      :ref="'bar-'+bar.id"
      app top right :timeout="bar.timeout"
      :color="bar.color"
      :style="'top:'+bar.top+'px'"
      max-width="150"
      @input="closingBar(bar)"
    >
      <v-icon class="pr-2">{{bar.icon || 'mdi-check-outline'}}</v-icon> {{bar.text}}
    </v-snackbar>
  </div>
</template>

<script>
export default {

  data() {
    return {
      lastId: -1,
      bars: []
    }
  },
  mounted() {
  },
  methods: {
    closingBar() {
      // console.log("Closing bar", bar)
      this.recalculateBars()
    },
    recalculateBars() {
      let bars = this.bars.map(b=>({...b})); // make a copy
      bars = bars.filter(b=>b.visible);
      let top = 0;
      for (const b of bars) {
        b.top = top;
        const els = this.$refs[`bar-${b.id}`];
        if (Array.isArray(els) && els.length === 1) {
          if (els[0].$el.firstElementChild) {
            const el = els[0].$el.firstElementChild;
            // console.log("bar", b.id, el.offsetHeight)
            top +=  el.offsetHeight + 10
          }
        }
      }
      // console.log("New list", bars)
      this.bars = bars;
    },
    addBar(bar) {
      bar = {id: this.lastId++, visible: true, timeout: 10000, ... bar};
      this.bars.push(bar);
      this.recalculateBars();
    },
    addError(text, opts= {}) {
      const bar = {color: "red", icon:"mdi-message-alert-outline", ...opts};
      bar.text = text;
      this.addBar(bar);
    },
    addConfirm(text, opts= {}) {
      const bar = {color: "green", icon:"mdi-check-outline", ...opts};
      bar.text = text;
      this.addBar(bar);
    },
  }
}
</script>

