<template>
  <div></div>
</template>

<script>
import { mapActions } from "vuex";
import { addVisitedWallet, getFirstWallet } from "./WalletsPage.vue";

export default {
  props: {
    value: Boolean
  },
  async mounted() {
    await this.checkAndReceiveAddress();
  },
  data() {
    return {

    }
  },
  watch: {
    value(newV, oldV) {
      if (newV && !oldV) { // change to true
        this.forwardToCustodyUi()
      }
    }
  },
  methods: {
    ...mapActions(["goToPage", "applyCurrentWalletAddress"]),

    async forwardToCustodyUi() {
      const vars = this.$store.getters.variables;
      if (!vars.walletCustodyUI) return;
      const target = new URL(vars.walletCustodyUI);
      target.searchParams.append("action", "select");
      target.searchParams.append("redirect_uri", window.location.href);
      window.location.assign(target.href);
    },
    async checkAndReceiveAddress() {
      const url = new URL(window.location)
      if (url.searchParams.has("address")) {
        await this.applyCurrentWalletAddress(url.searchParams.get("address"));
        addVisitedWallet(url.searchParams.get("address"));
        url.searchParams.delete("address")
        window.history.replaceState(null, "", url.href);
      } else { // see if we can use the first address from the storeage
        const first = await getFirstWallet();
        if(first) {
          await this.applyCurrentWalletAddress(first);
        } else {
          await this.applyCurrentWalletAddress("0x0000000000000000000000000000000000000000")
        }
      }
    }
  }
}
</script>

