<template>
  <span v-if="value"><slot>{{value}}</slot>
    <v-icon v-if="blockchainExplorer" class="pb-1" small color="primary" @click="openInNew">mdi-open-in-new</v-icon>
    <v-icon v-else small color="red" class="pb-1" title="Define the blockchain explorer!">mdi-open-in-new</v-icon>
  </span>
</template>

<script>
export default {
  props: {
    value:String,
    type:String
  },
  computed: {
    blockchainExplorer() {
      return this.$store.getters.variables?.blockchainExplorer;
    }
  },
  methods: {
    openInNew() {
      let action = "address/";
      if(this.type) {
        action=`${this.type}/`;
      } else {
        if(this.value.length > 42) action = "tx/"
      }
      action = action + this.value;
      const url = new URL(action, this.blockchainExplorer);
      window.open(url.href, "_blank");
    }
  }
}
</script>

